import { BpmnModel } from "./BpmnModel";
import { Gateway } from "./Gateway";
import { GatewayType } from "./GatewayType";

export class ParallelGateway extends Gateway {
    constructor(model: BpmnModel) {
        super(model);
    }

    readonly ClrTypeID: string = "bpmn.ParallelGateway";
    IncomingGatewayType: string = GatewayType.Parallel;
    OutgoingGatewayType: string = GatewayType.Parallel;

    NewInstance(): ParallelGateway {
        return new ParallelGateway(this.Model);
    }
}